@import '../../styles/index.scss';

.App {
    @include borders(white, #999);
    background: #c2c2c2;
    padding: 16px;
}

.Header {
    @include borders(#7b7b7b, white);
    align-items: center;
    background: #c0c0c0;
    display: flex;
    justify-content: space-between;
    padding: 10px 12px;
}

.Body {
    @include borders(#7b7b7b, white);
    display: grid;
    grid-template-columns: repeat(16, 1fr);
    grid-template-rows: repeat(16, 1fr);
    margin-top: 16px;
}

.Face {
    @include borders;
    @include buttons;
    align-items: center;
    cursor: pointer;
    display: flex;
    font-size: 35px;
    height: 52px;
    justify-content: center;
    width: 52px;
}
